import React from "react"
import {Link} from "gatsby"

import Navbar from 'react-bootstrap/Navbar'
import { Nav, NavDropdown } from "react-bootstrap"

export default function NavbarEasy(props){
    return (
        <Navbar centered bg="dark" variant="dark">
            <Navbar.Brand>
                <img
                    src="/leaf.png"
                    width="42"
                    height="30"
                    className="d-inline-block align-top"
                    alt=""
                />
            </Navbar.Brand>
            <Navbar.Brand>Gabe Caldwell</Navbar.Brand>
            <Nav className="mr-auto">
                {props.items.map((item, index) => (
                    <Nav.Item>
                        <Nav.Link href={props.links[index]}>{item}</Nav.Link>
                        
                    </Nav.Item>
                ))}          

    
            </Nav>
        </Navbar>
    )
}